@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100..900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@100..900&display=swap");
* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

html {
  scroll-behavior: smooth;
}

body {
  background: #F8F8F8;
}

* {
  line-height: 130%;
  color: #242220;
}

main {
  display: flex;
}

.container {
  max-width: 1260px;
  width: 100%;
  padding: 30px 10px 20px 90px;
  margin: 0 auto;
  transition: all ease 0.5s;
}
.container_active {
  padding: 30px 10px 0 10px;
}

.login {
  padding: 0 20px;
  width: 100vw;
  height: 100vh;
  background: url("../img/login__bg.png");
  background-size: cover;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-direction: column;
}
.login__top {
  text-align: center;
  margin-bottom: 50px;
}
.login__logo {
  width: 44px;
  height: 44px;
  border: 1px solid rgba(36, 34, 32, 0.8509803922);
  border-radius: 50%;
  margin: 0 auto 24px auto;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}
.login__title {
  font-family: "Inter", sans-serif;
  font-size: 30px;
  color: #242220;
  margin-bottom: 12px;
}
.login__text {
  font-family: "Inter", sans-serif;
  font-size: 16px;
  color: #242220;
}
.login__content {
  display: flex;
  flex-direction: column;
  gap: 20px;
  max-width: 460px;
  width: 100%;
}
.login__button {
  font-family: "Inter", sans-serif;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  gap: 15px;
  background: #FFFFFF;
  display: flex;
  width: 100%;
  padding: 20px 0;
  border-radius: 5px;
  font-size: 20px;
  color: rgba(36, 34, 32, 0.8509803922);
}
.login__privacy {
  font-family: "Inter", sans-serif;
  position: absolute;
  bottom: 15px;
  right: 10px;
  font-size: 12px;
  color: #000;
}
@media screen and (max-width: 450px) {
  .login__button {
    font-size: 14px;
  }
}

.sidebar {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 24px;
  position: fixed;
  left: 0;
  top: 0;
  width: 80px;
  height: 100vh;
  padding: 24px 18px;
  background: #FFFFFF;
  border-right: 1px solid rgba(36, 34, 32, 0.0509803922);
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  transition: all ease 0.5s;
}
.sidebar_hide {
  left: -80px;
}
.sidebar__arrow {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  position: absolute;
  top: 32px;
  right: -17px;
  width: 28px;
  height: 28px;
  background: #FFFFFF;
  border: 1px solid #F8F8F8;
  border-radius: 8px;
  cursor: pointer;
  transition: all ease 0.5s;
}
.sidebar__arrow svg {
  transform: rotate(180deg);
}
.sidebar__arrow_hide {
  transform: rotate(-180deg);
}
.sidebar__logo {
  width: 44px;
  height: 44px;
  border: 1px solid rgba(36, 34, 32, 0.8509803922);
  border-radius: 50%;
  margin: 0 auto 0 auto;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}
.sidebar__line {
  width: 32px;
  height: 2px;
  border-right: 1px;
  background: rgba(36, 34, 32, 0.0509803922);
}
.sidebar__menu {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 8px;
}
.sidebar__link {
  position: relative;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  width: 40px;
  height: 40px;
  border-radius: 8px;
  transition: all ease 0.5s;
}
.sidebar__link svg path {
  transition: all ease 0.5s;
}
.sidebar__link:hover {
  background: rgba(36, 34, 32, 0.0509803922);
}
.sidebar__link:hover svg path {
  fill: #242220;
}
.sidebar__link:hover .sidebar__tooltip {
  visibility: visible;
  opacity: 1;
}
.sidebar__link_active svg path {
  fill: #1890FF;
}
.sidebar__link_active:hover svg path {
  fill: #1890FF;
}
.sidebar__tooltip {
  font-family: "Inter", sans-serif;
  visibility: hidden;
  font-size: 14px;
  background-color: #242220;
  color: #FFFFFF;
  text-align: center;
  padding: 5px 18px;
  border-radius: 6px;
  position: absolute;
  z-index: 1;
  bottom: 6px;
  left: 140%;
  opacity: 0;
  transition: opacity 0.3s;
}
.sidebar__tooltip::after {
  content: "";
  position: absolute;
  top: 35%;
  left: -5px;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent #242220 transparent transparent;
}
.sidebar__bottom {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 8px;
}
.sidebar__block {
  position: relative;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  width: 40px;
  height: 40px;
  border-radius: 8px;
  transition: all ease 0.5s;
}
.sidebar__block:hover {
  background: rgba(36, 34, 32, 0.0509803922);
}

.header {
  margin-bottom: 30px;
}
.header__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 270px;
  width: 100%;
  margin-bottom: 40px;
  margin-left: auto;
}
.header__svg {
  cursor: pointer;
}
.header__profile {
  display: flex;
  align-items: center;
  gap: 10px;
}
.header__icon {
  max-width: 40px;
  max-height: 40px;
  border-radius: 50%;
  border: 2px solid #242220;
  overflow: hidden;
}
.header__icon img {
  width: 100%;
  height: 100%;
}
.header__name {
  font-family: "Inter", sans-serif;
  font-size: 16px;
  font-weight: 400;
}
.header__bottom {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 20px;
}
.header__title {
  display: flex;
  align-items: center;
  gap: 10px;
  font-family: "Inter", sans-serif;
  font-size: 24px;
}
.header__title a {
  display: flex;
}
.header__title svg {
  cursor: pointer;
}
.header__block {
  max-width: 705px;
  width: 100%;
  display: flex;
  gap: 20px;
}
.header__button {
  font-family: "Inter", sans-serif;
  color: #FFFFFF;
  font-size: 16px;
  padding: 8px 16px;
  border-radius: 8px;
  background: #1890FF;
}
.header__tabs {
  display: flex;
}
.header__tab {
  display: flex;
  gap: 8px;
  font-family: "Inter", sans-serif;
  font-size: 16px;
  padding: 8px 16px;
  border: 1px solid rgba(36, 34, 32, 0.2);
  cursor: pointer;
  transition: all ease 0.5s;
}
.header__tab:hover {
  border-color: #1890FF;
}
.header__tab:first-child {
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
  margin-right: -1px;
}
.header__tab:last-child {
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
  margin-left: -1px;
}
.header__tab_active {
  border-color: #1890FF;
}
.header__number {
  font-family: "Inter", sans-serif;
  font-size: 14px;
  border-radius: 12px;
  background: #E0EFFE;
  padding: 2px 6px;
}
@media screen and (max-width: 768px) {
  .header__block {
    justify-content: space-between;
    align-items: baseline;
  }
  .header__tabs {
    flex-direction: column;
  }
  .header__tab:first-child {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    margin-right: 0;
  }
  .header__tab:last-child {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    margin-left: 0;
  }
}

.table {
  width: 100%;
  margin-bottom: 30px;
}
.table table {
  width: 100%;
}
.table__head {
  width: 100%;
  display: flex;
  background: rgba(36, 34, 32, 0.0509803922);
  padding: 20px 0;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
}
.table__head .table__cell {
  font-size: 16px;
  border-right: 1px solid rgba(36, 34, 32, 0.2);
  color: #242220;
}
.table__head .table__cell:last-child {
  border: 0;
}
.table__row {
  width: 100%;
  display: flex;
  padding: 16px 0;
  background: #FFFFFF;
  border-bottom: 1px solid rgba(36, 34, 32, 0.0509803922);
}
.table__row:last-child {
  border: 0;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}
.table__cell {
  font-family: "Inter", sans-serif;
  font-size: 14px;
  padding-right: 12px;
  padding-left: 12px;
  display: flex;
  gap: 10px;
  align-items: center;
  overflow: hidden;
  white-space: nowrap;
}
.table__cell_first {
  flex-basis: 28%;
  color: #1890FF;
}
.table__cell_second {
  flex-basis: 12%;
}
.table__cell_third {
  flex-basis: 18%;
}
.table__cell_fourth {
  flex-basis: 16%;
}
.table__cell_fifth {
  flex-basis: 9.3%;
  display: flex;
  justify-content: center;
}
.table__cell_sixth {
  flex-basis: 9.3%;
  display: flex;
  justify-content: center;
}
.table__cell_seventh {
  flex-basis: 7.3%;
  display: flex;
  justify-content: center;
}
@media screen and (max-width: 1140px) {
  .table {
    overflow-x: scroll;
  }
}

.pagination {
  display: flex;
  max-width: 450px;
  width: 100%;
  margin-left: auto;
}
.pagination__content {
  display: flex;
  align-items: center;
  gap: 10px;
}
.pagination__item {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  font-family: "Inter", sans-serif;
  font-size: 18px;
  width: 40px;
  height: 40px;
  border: 1px solid transparent;
  border-radius: 5px;
  transition: all ease 0.5s;
  cursor: pointer;
}
.pagination__item_active, .pagination__item:hover {
  color: #1890FF;
  border: 1px solid #1890FF;
}
.pagination__prev, .pagination__next {
  font-family: "Inter", sans-serif;
  font-size: 18px;
  cursor: pointer;
}
.pagination__prev {
  color: rgba(36, 34, 32, 0.3019607843);
  cursor: not-allowed;
}

.conference-settings {
  border-top: 1px dashed rgba(36, 34, 32, 0.2);
}
.conference-settings__block {
  display: flex;
  align-items: baseline;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 10px;
  padding: 30px 0;
  border-bottom: 1px dashed rgba(36, 34, 32, 0.2);
}
.conference-settings__block:last-child .conference-settings__content {
  flex-direction: row;
  flex-wrap: wrap;
  gap: 30px;
}
.conference-settings__title {
  font-family: "Montserrat", sans-serif;
  font-size: 20px;
  margin-bottom: 20px;
}
.conference-settings__content {
  display: flex;
  flex-direction: column;
  gap: 20px;
  max-width: 700px;
  width: 100%;
}
.conference-settings__label {
  display: flex;
  flex-direction: column;
  gap: 8px;
  font-family: "Inter", sans-serif;
  font-size: 16px;
}
.conference-settings__label b {
  color: #ED1D23;
  margin-right: 4px;
}
.conference-settings__input {
  background: transparent;
  border: 1px solid rgba(36, 34, 32, 0.2);
  padding: 13px 12px;
  border-radius: 8px;
}
.conference-settings__count {
  text-align: right;
  font-size: 12px;
  color: rgba(36, 34, 32, 0.5019607843);
}
.conference-settings__group {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
.conference-settings__group .conference-settings__label {
  max-width: 220px;
  width: 100%;
}
.conference-settings__accept {
  display: flex;
  gap: 15px;
  cursor: pointer;
}
.conference-settings__accept input {
  background: transparent;
  width: 24px;
  height: 24px;
}
.conference-settings__accept p {
  font-family: "Inter", sans-serif;
  font-size: 16px;
}
.conference-settings__participants {
  display: flex;
  flex-wrap: wrap;
  column-gap: 15px;
  row-gap: 10px;
}
.conference-settings__participant {
  display: flex;
  align-items: center;
  gap: 4px;
  font-family: "Inter", sans-serif;
  font-size: 14px;
  color: #1890FF;
  padding: 4px 8px;
  background: #E6F7FF;
  border: 1px solid #91D5FF;
  border-radius: 2px;
}
.conference-settings__checkbox {
  max-width: 300px;
  width: 100%;
  font-family: "Inter", sans-serif;
  font-size: 16px;
  font-weight: 400;
  display: flex;
  flex-direction: column;
  gap: 15px;
}
.conference-settings__checkbox span {
  margin-bottom: 5px;
  display: flex;
  gap: 12px;
}
.conference-settings__checkbox label {
  display: flex;
  align-items: center;
  gap: 15px;
}
.conference-settings__checkbox label input {
  width: 24px;
  height: 24px;
  background: transparent;
}
@media screen and (max-width: 768px) {
  .conference-settings__group {
    flex-wrap: wrap;
    gap: 20px;
    align-items: baseline;
  }
}